/**
 * 判断两个数组是否相交
 * @param array1 
 * @param array2 
 * @returns 
 */
export const areArraysIntersected = (array1: any[], array2: any[]) => array1.some((item) => array2.includes(item));

/**
 * 判断两个数组包含内容是否相同
 * @param array1 
 * @param array2 
 * @returns 
 */
export const areArraysContentEqual = (array1: any[], array2: any[]) => {
  if (array1.length !== array2.length) {
    return false;
  }

  for (let index = 0; index < array1.length; index++) {
    const element1 = array1[index];
    const element2 = array2[index];
    if (element1 !== element2) {
      return false;
    }

  }

  return true;
};